.intro {
  background: linear-gradient(to bottom, #3CB4E5 5%, #00A0D2 40%, #6BC4E8);
  overflow: hidden;
  
  @include vp-1023 {
    position: relative;
  }

  .container {
    @include vp-1023 {
      padding: 0;
    }
  }

  &__wrapper {
    padding: 0;
    display: flex;
    gap: 20px;

    @include vp-1023 {
      flex-direction: column;
      gap: 30px;
    }

    @include vp-419 {
      gap: 10px;
    }
  }

  &__banner {
    width: 790px;
    height: 800px;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media screen and (max-width: 1550px) {
      width: 700px;
      height: 750px;
    }

    @media screen and (max-width: 1400px) {
      width: 600px;
      height: 650px;
    }

    @include vp-1199 {
      width: 500px;
      height: 600px;
    }

    @include vp-1023 {
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    padding: 150px 0 100px;

    @media screen and (max-width: 1550px) {
      padding: 120px 0 70px;
    }

    @media screen and (max-width: 1400px) {
      padding: 60px 0 50px;
    }

    @include vp-1199 {
      padding: 40px 0 30px;
    }

    @include vp-1023 {
      padding: 0 20px 40px;
    }

    @include vp-419 {
      padding-bottom: 10px;
    }
  }

  &__title {
    font-family: $ff-monts;
    font-size: 64px; 
    line-height: 64px;
    color: $color-default-white;
    margin: 0 0 50px;
    text-align: center;
    @media screen and (max-width: 1550px) {
      font-size: 50px;
    }

    @media screen and (max-width: 1400px) {
      font-size: 40px;
      line-height: 50px;
      margin: 0 0 40px;
    }

    @include vp-767 {
      font-size: 32px;
      margin-bottom: 10px;
      line-height: 32px;
    }
  }

  &__text {
    font-family: $ff-inter;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: $color-default-white;
    margin-bottom: 50px;

    @media screen and (max-width: 1550px) {
      font-size: 20px;
    }

    @media screen and (max-width: 1550px) {
      font-size: 18px;
      margin-bottom: 40px;
    }

    @include vp-767 {
      font-size: 16px;
      margin-bottom: 20px;
    }

    @include vp-419 {
      font-size: 14px;
      margin-bottom: 10px;
    }
  }

  &__link {
    display: block;
    text-align: center;
    border-radius: 50px;
    max-width: 385px;
    width: 100%;
    background: $color-default-white;
    font-family: $ff-inter;
    font-size: 16px;
    line-height: 19px;
    font-weight: 700;
    color: #00A0D2;
    text-transform: uppercase;
    padding: 19px 20px;
    margin: 0 auto;
    border: 1px solid transparent;
    transition: .3s;
  }

  &__link:hover {
    color: $color-default-white;
    background: #3CB4E5;
    border: 1px solid $color-default-white;
  }
}
