.footer {
    background: #FFFBFB;
    margin-top: 50px;
    &__wrapper {
        padding: 50px 0;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
    }

    &__block {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__logo {
        width: 250px;
        height: 113px;
        img {
            width: 250px;
            height: 113px;
            object-fit: cover;
        }
    }

    &__low {
        max-width: 300px;
        font-family: $ff-inter;
        font-size: 24px;
        line-height: 29px;
        color: $color-default-black;
    }

    &__info-text {
        width: 100%;
        font-family: $ff-inter;
        font-size: 24px;
        line-height: 29px;
        color: $color-default-black;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 30px;
        justify-self: center;
    }

    &__link {
        font-family: $ff-inter;
        font-size: 24px;
        line-height: 28px;
        color: $color-default-black;
        border-bottom: 1px solid $color-default-black;
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        gap: 30px;
        justify-self: center;
    }

    &__phone,
    &__mail,
    &__adress {
        font-family: $ff-inter;
        font-size: 24px;
        line-height: 29px;
        color: $color-default-black;
        display: flex;
        flex-direction: row-reverse;
        align-items: flex-start;
        justify-content: start;
        gap: 20px;
    }

    &__adress {
        max-width: 350px;
    }

    &__social {
        display: flex;
        gap: 30px;
    }

    &__more {
        background: $color-default-white;
        padding: 20px 0;
    }

    &__bottom {
        width: 100%;
        display: inline-grid;
        grid-template-columns: 1.3fr 1.4fr 1fr;
        gap: 10px;
        a {
            width: fit-content;
            font-family: $ff-inter;
            font-size: 24px;
            line-height: 28px;
            color: $color-default-black;
            border-bottom: 1px solid $color-default-black;
        }

        a:nth-child(2) {
            justify-self: center;
        }
    }

}
