.products {
    
    overflow: hidden;
    &__wrapper {
        padding: 50px 0;

        @include vp-767 {
            padding: 25px 0;
        }
    }
    
    &__title {
        font-family: $ff-monts;
        font-size: 40px;
        font-weight: 600;
        line-height: 40px;
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 32px;
        }

        @include vp-419 {
            font-size: 24px;
            margin-bottom: 5px;
            text-align: center;
        }
    }

    &__content {
        position: relative;
    }
    
    
    &__slider {
        display: flex;
        margin-bottom: 50px;
        padding: 10px;
        width: 100%;
        height: 100%;
    }
    
    .swiper-wrapper {
        height: auto;
    }

    .swiper-slide {
        display: flex;
        flex-grow: 1;
    }

    .swiper-pagination {
        top: 0;
        right: 0px;
        left: auto;
        display: flex;
        align-items: center;
        justify-content: end;
        gap: 20px;
        height: 21px;
        margin-top: -40px;

        @include vp-767 {
            margin-top: 0;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            justify-content: center;
            padding-top: 10px;
        }
    }
    
    
    .swiper-pagination span {
        width: 21px;
        height: 21px;
        background: #6BC4E8;
        border: none;
        border-radius: 100px;
        display: block;
        margin: 0 !important;
    }
    
    .swiper-pagination span.swiper-pagination-bullet-active {
        width: 21px;
        height: 21px;
        background: #00A0D2;
        border: none;
        border-radius: 100px;
        display: block;
    }

    &__link {
        display: block;
        max-width: 385px;
        width: 100%;
        background: #00A3DD;
        font-family: $ff-inter;
        font-size: 16px;
        font-weight: 700;
        line-height: 19px;
        text-transform: uppercase;
        color: $color-default-white;
        padding: 19px;
        border-radius: 50px;
        text-align: center;
        margin: 0 auto;
        transition: .3s;
    }

    &__link:hover {
        opacity: 0.8;
    }

}