.articles-card {
    display: flex;

    @include vp-767 {
        width: 100%;
    }
    &__articles-card {
        width: 100%;
        display: flex;
        max-width: 500px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include vp-767 {
            width: 100%;
        }
    }

    &__img {
        border-radius: 10px;
        width: 100%;
        height: 460px;
        overflow: hidden;
        margin-bottom: 20px;
        img {
            width: 100%;
            height: 460px;
            object-fit: cover;
        }

        @include vp-589 {
            height: 300px;
            img {
                height: 100%;
            }
        }
    }

    &__info {
        width: 100%;
        display: flex;
        flex-direction: column;
        flex: 1;
        text-align: center;
        padding: 0 10px 20px;
    }

    &__title {
        font-family: $ff-monts;
        font-size: 24px;
        font-weight: 600;
        line-height: 24px;
        color: #0A0A0A;
        margin: 0 auto 20px;
        width: 90%;

        @include vp-589 {
            font-size: 20px;
            text-align: left;
            margin: 0 0 10px;
            width: 100%;
        }
    }

    &__link {
        display: block;
        max-width: 250px;
        width: 100%;
        border: 1px solid #00A0D2;
        border-radius: 50px;
        font-family: $ff-inter;
        font-size: 16px;
        font-weight: 700;
        color: #00A0D2;
        text-transform: uppercase;
        padding: 19px;
        margin: 0 auto;
        transition: .3s;
    }

    &__link:hover {
        border: 1px solid transparent;
        background: #00A0D2;
        color: $color-default-white;
    }
}