.form {
    background: #00A3DD;
    margin: 100px 0 100px;
    
    .container {
        @include vp-1199 {
            padding: 0;
        }
    }
    &__wrapper {
        display: flex;
        position: relative;
        @include vp-1199 {
            flex-direction: column;
            gap: 40px;
        }
    }

    &__info {
        padding: 50px 0;
        max-width: 390px;
        width: 100%;

        @media screen and (max-width: 1550px) {
            padding: 30px 0;
        }

        @include vp-1199 {
            padding: 30px 20px 0;
            max-width: 100%;
        }
    }

    &__title {
        font-family: $ff-monts;
        font-size: 40px;
        line-height: 40px;
        font-weight: 600;
        color: $color-default-white;
        margin: 0 0 20px;

        @include vp-1199 {
            text-align: center;
        }
    }

    &__text {
        font-family: $ff-inter;
        font-size: 24px;
        line-height: 28px;
        color: $color-default-white;
        margin: 0 0 20px;
        @include vp-1199 {
            text-align: center;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .custom-input__label {
        font-family: $ff-inter;
        font-size: 18px;
        line-height: 21px;
        color: $color-default-white;
        margin: 0 0 10px;
    }

    .custom-input input {
        border-radius: 99px;
    }

    .custom-checkbox label {
        align-items: flex-start;
        gap: 5px;
    }

    .custom-checkbox__label {
        font-family: $ff-inter;
        font-size: 18px;
        line-height: 21px;
        color: $color-default-white;
        font-weight: 400;
    }

    .custom-checkbox__icon {
        width: 30px;
        height: 30px;
        background: transparent;
        border: 2px solid $color-default-white;
        svg {
            background: $color-default-white;
        }
    }

    &__btn {
        max-width: 385px;
        width: 100%;
        background: $color-default-white;
        border-radius: 50px;
        font-family: $ff-inter;
        font-size: 16px;
        font-weight: 700;
        color: #00A0D2;
        text-transform: uppercase;
        padding: 19px;
        margin: 0 auto;
        transition: .3s;
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__image {
        position: absolute;
        right: 0;
        bottom: -25px;
        width: 1050px;
        height: 750px;
        // border-radius: 20px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media screen and (max-width: 1550px) {
            width: 900px;
            height: 700px;
        }

        @media screen and (max-width: 1400px) {
            width: 750px;
            height: 700px;
        }

        @include vp-1279 {
            width: 650px;
            height: 700px;
        }

        @include vp-1199 {
            position: static;
            width: 100%;
            height: 100%;
            // border-radius: 0px !important;
            overflow: visible;
        }
    }
}