.products-card {
    display: flex;
    // max-width: 385px;
    // width: 100%;

    @include vp-767 {
        width: 100%;
    }
    &__products-card {
        max-width: 385px;
        width: 100%;
        display: flex;

        // @include vp-767 {
        //     width: 100%;
        // }
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include vp-767 {
            width: 100%;
        }

    }

    &__img {
        position: relative;
        z-index: 1;
        border-radius: 10px;
        width: 330px;
        overflow: hidden;
        img {
            object-fit: cover;
        }

        @include vp-419 {
            width: 280px;
        }

        @include vp-374 {
            width: 250px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        flex: 1;
        text-align: center;
        background: #FFFBFB;
        padding: 0 20px 20px;
        margin-top: -30px;
        transition: .3s;
        border-radius: 10px 10px 30px 30px;

        @include vp-767 {
            max-width: 385px;
            width: 100%;
        }
    }

    &__info:hover {
        box-shadow: 0px 33px 33px 0px rgba(0, 0, 0, 0.09);
    }

    &__title {
        position: relative;
        z-index: 3;
        margin: 0 0 40px;
        font-family: $ff-inter;
        font-size: 18px;
        line-height: 21px;
        font-weight: 400;
        color: #0A0A0A;

        @include vp-589 {
            margin-bottom: 20px;
        }
    }

    &__text {
        font-family: $ff-monts;
        font-size: 24px;
        font-weight: 600;
        line-height: 24px;
        color: #0A0A0A;
        margin: 0 auto 20px;
        width: 90%;

        @include vp-589 {
            font-size: 20px;
            margin-bottom: 10px;
        }

        @include vp-419 {
            font-size: 16px;
        }
    }

    &__rating-box {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
        gap: 4px;
    }

    &__rating {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 4px;
    }

    &__rating-total {
        font-family: $ff-inter;
        font-size: 24px;
        line-height: 29px;
        color: #0A0A0A;
    }

    &__link {
        display: block;
        max-width: 345px;
        width: 100%;
        border: 1px solid #00A0D2;
        border-radius: 50px;
        font-family: $ff-inter;
        font-size: 16px;
        font-weight: 700;
        color: #00A0D2;
        text-transform: uppercase;
        padding: 19px;
        transition: .3s;

        @include vp-1199 {
            margin: 0 auto;
        }
    }

    &__link:hover {
        color: $color-default-white;
        background: #00A0D2;
    }
}