.header {
  position: relative;
  z-index: 11;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $color-default-white;
  &.is-open {
    .main-nav__toggle {
      span {
        &:nth-child(1) {
          top: 10px;
          transform: rotate(-45deg) translate(0, -50%);
        }

        &:nth-child(2) {
          opacity: 0;
          visibility: hidden;
        }

        &:nth-child(3) {
          top: 8px;
          transform: rotate(45deg) translate(0, 50%);
        }
      }
    }

    .main-nav__list {
      transform: translateX(0);
    }
  }

  &__wrapper {
    width: 100%;
    margin: 0 auto;
    padding: 20px 0;

    @include vp-1023 {
      max-width: unset;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include vp-767 {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  &__bottom {
    overflow: hidden;
  }

  &__logo {
    position: relative;
    display: block;
    width: 150px;
    height: 60px;
    margin-right: 40px;
    flex-shrink: 0;
    @include vp-1399 {
      margin-right: 5px;
      width: 130px;
    }

    @include vp-1199 {
      margin-right: 30px;
    }

    @include vp-419 {
      width: 100px;
    }

    @include vp-389 {
      width: 90px;
      height: 40px;
      margin-right: 10px;
    }

    svg,
    img {
      display: block;
      width: 150px;
      height: 60px;
      object-fit: contain;
      @include vp-1399 {
        width: 130px;
      }

      @include vp-389 {
        width: 110px;
        height: 40px;
      }
    }
  }

  
  &__icons {
    display: flex;
    align-items: center;
    gap: 50px;

    @include vp-767 {
      flex-direction: column;
      grid-column: 2;
      grid-row: 1;
    }
  }

  &__form {
    position: relative;
    width: 500px;
    .header__btn.search {
      display: flex;
      align-items: center;
      position: absolute;
      right: 10px;
      top: 13px;
      z-index: 1;
      width: 25px;
      img {
        width: 25px;
        height: 25px;
      }
  
    
      @include vp-767 {
        border-bottom: none;
        justify-content: end !important;
        top: 10px;
      }

      @include vp-419 {
        top: 7px;
      }
      
    }

    @include vp-1199 {
      width: 250px;
    }

    @include vp-589 {
      width: 180px;
    }

    @include vp-419 {
      width: 150px;
    }
    
  }

  &__btn {
   
    transition: .3s;
    img {
      width: 25px;
      height: 25px;
      object-fit: contain;
    }

    p {
      display: none;
    }

    &:hover {
      opacity: 0.8;
    }

    @include vp-767 {
      border-bottom: 1px solid #6BC4E8;
      width: 100%;
      display: flex !important;
      align-items: center;
      justify-content: center !important;
      img {
        text-align: center;
      }

      p {
        display: block;
      }
      
    }
  }

  

  .custom-input {
    
    opacity: 0;
    visibility: hidden;
    transition: .3s;
  }

  .custom-input.header__search-input.active {
    opacity: 1;
    visibility: visible;
  }

  .custom-input input {
    border-radius: 20px;
    border: 1px solid $color-default-black;
    padding: 10px 40px 10px 20px;

    @include vp-419 {
      padding: 7px 15px;
    }
  }
}
