.collection {
    &__wrapper {
        padding: 117px 0 50px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        gap: 20px;
        width: 100%;
        @include vp-1399 {
            padding: 60px 0 50px;
        }

        @include vp-767 {
            padding: 50px 0 25px;
        }

        @include vp-419 {
            gap: 10px;
        }
    }

    &__title {
        font-family: $ff-monts;
        font-size: 40px;
        font-weight: 600;
        line-height: 40px;
        margin: 0;
        grid-column: 1;

        @include vp-767 {
            grid-row: 1;
            font-size: 32px;
        }

        @include vp-419 {
            font-size: 24px;
            grid-column: 1/4;
            justify-self: center;
        }
    }

    &__link {
        grid-column: 2;
        justify-self: end;
        display: block;
        text-align: center;
        border-radius: 50px;
        max-width: 385px;
        width: 100%;
        background: #00A0D2;
        font-family: $ff-inter;
        font-size: 16px;
        line-height: 19px;
        font-weight: 700;
        color: $color-default-white;
        text-transform: uppercase;
        padding: 19px 20px;
        transition: .3s;

        @include vp-767 {
            grid-row: 3;
            grid-column: 1/4;
            justify-self: center;
        }
    }

    &__link:hover {
        opacity: 0.8;
    }

    &__list {
        grid-column: 1/4;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;

        @include vp-1399 {
            gap: 10px;
        }

        @include vp-1199 {
            grid-template-columns: 1fr;
        }

        @include vp-767 {
            grid-row: 2;
        }
    }

    &__item {
        position: relative;
        transition: .3s;
    }

    &__item:hover {
        box-shadow: 0px 33px 33px 0px rgba(0, 0, 0, 0.09);
    }

    &__image {
        border-radius: 10px;
        overflow: hidden;
        img {
            width: 100%;
            object-fit: cover;
        }

        @include vp-1199 {
            width: 100%;
            height: 700px;
        }

        @include vp-589 {
            height: 500px;
        }

        @include vp-419 {
            height: 100%;
        }
    }

    &__title-bottom {
        position: absolute;
        bottom: 0;
        font-family: $ff-monts;
        font-size: 64px;
        line-height: 64px;
        font-weight: 700;
        color: $color-default-white;
        background: #3CB4E5;
        border-radius: 0px 10px 10px 0;
        padding: 20px 40px;
        margin: 0 0 20px;

        @include vp-1399 {
            font-size: 50px;
        }

        @include vp-767 {
            font-size: 32px;
            padding: 10px 20px;
        }

        @include vp-419 {
            font-size: 24px;
            line-height: 24px;
        }
    }
}